.App {
  font-family: sans-serif;
  text-align: center;
}

.display {
  width: 300px;
  height: 60px;
  background-color: #add8e6;
  border: solid 3px #191970;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50px;
  font-size: 1em;
  color: #191970;
}

.btns {
  width: 150px;
  height: 40px;
  margin: 5px;
  padding: 5px;
  border-radius: 50px;
}

.btn-start {
  background-color: #00008b;
  color: #ffffff;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.btn-smaller {
  background-color: #ff0000;
  color: #ffffff;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.btn-bigger {
  background-color: #006400;
  color: #ffffff;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.btn-guessed {
  background-color: #8b008b;
  color: #ffffff;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
